import { Component, h, Host, Prop } from '@stencil/core';
import { About, AboutItems } from '../../../global/data/content';

@Component({
	tag: 'ks-content-about',
	styleUrl: 'about.css'
})
export class ContentAbout {
    @Prop() content: About;
    @Prop({mutable: true, reflect: true}) expanded: boolean = false;

    render() {
        const image = this.content.image;
        const heading = this.content.heading;
        const paragraph = this.content.paragraph;

        return <Host class={this.content.style}>
            {image ? 
                <ks-img3 class="main-image"
                    fit="contain"
                    image={image}
                    webp={this.content.webp}
                    alt={this.content.heading}
                    width={this.content.width}
                    height={this.content.height}
                ></ks-img3>
            : null}

            {heading ? <h2>{heading}</h2> : null}
            {paragraph ? <p>{paragraph}</p> : null}

            <div class="items">
                {this.content.items.map(item => this.item(item))}
            </div>

            {this.content.style == 'expand' ? 
                <ks-button border round
                    name={this.expanded ? "Zwiń" : "Rozwiń"}
                    onClick={()=>this.expanded = !this.expanded}
                ></ks-button>
            : null}
        </Host>;
    }

	item(item: AboutItems) {
        const image = item.image;
        const heading = item.heading;
        const paragraph = item.paragraph;

		return <div class="item">
            {image ?
                <ks-img3 fit="contain"
                    image={item.image}
                    webp={item.webp}
                    alt={item.heading}
                    width={item.width}
                    height={item.height}
                ></ks-img3>
            : null}

            {heading ? <h3>{heading}</h3> : null}
            {paragraph ? <p>{paragraph}</p> : null}
		</div>;
	}
}