import { Component, h, Prop } from '@stencil/core';
import { Thumbnail } from '../../../global/data/content';

@Component({
	tag: 'ks-content-thumbnails',
	styleUrl: 'thumbnails.css'
})
export class ContentThumbnail {
    @Prop() content: Thumbnail[];

    render() {
        return this.content.map(item =>
            <a class="thumbnail" href={item.link}>
                <ks-img3 fit="cover"
                    image={item.image}
                    webp={item.webp}
                    alt={item.name}
                    width={item.width}
                    height={item.height}
                ></ks-img3>
            </a>
        );
    }
}