ks-content-banners {
    display: block;
    margin: auto;
    position: relative;
}

ks-content-banners .banner {
    position: relative;
    height: auto;

    max-height: 550px;
    height: 100vw;
}

ks-content-banners .banner ks-img3 {
    max-height: 550px;
    height: 100%;
}

ks-content-banners .banner .text {
    position: absolute;
    inset: 0px;

    display: flex;
    flex-direction: column;

    max-width: 1000px;
    box-sizing: border-box;
    margin: auto;
    padding: 50px;
}

ks-content-banners .banner .text.horizontal-left   { align-items: flex-start; text-align: left; }
ks-content-banners .banner .text.horizontal-center { align-items: center; text-align: center;}
ks-content-banners .banner .text.horizontal-right  { align-items: flex-end; text-align: right;}

ks-content-banners .banner .text.vertical-top      { justify-content: flex-start; }
ks-content-banners .banner .text.vertical-center   { justify-content: center; }
ks-content-banners .banner .text.vertical-bottom   { justify-content: flex-end; }

ks-content-banners .banner .text .mainText {
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(7px);
    padding: 10px 20px;
    font-size: 22px;
    font-family: var(--font-emphasis);
}

ks-content-banners .banner .text .aboveText {
    text-shadow: 0px 0 11px black, 0px 0 4px black;
    font-size: 18px;
    margin-bottom: 5px;
}

@media screen and (max-width: 640px) {
    ks-content-banners .banner .text {
        padding: 15px;
    }

    ks-content-banners .banner .text .mainText {
        padding: 6px 15px;
        font-size: 18px;
    }

    ks-content-banners .banner .text .aboveText {
        font-size: 16px;
        margin-bottom: 3px;
    }
}

/* Banner container */


ks-content-banners .swiper-wrapper {
    max-height: 550px;
    height: 100%;
}

ks-content-banners a {
    position: relative;
}

ks-content-banners .pagination {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0px;

    height: 35px;
    display: flex;
    justify-content: center;

    opacity: 0;
    transition: opacity 0.5s ease;
}

ks-content-banners[loaded] .pagination {
    opacity: 1;
}

ks-content-banners .swiper-pagination {
    display: flex;
    align-items: center;
    position: relative;
    padding: 10px;
}

ks-content-banners .swiper-pagination-bullet  {
    height: 10px;
    width: 10px;
    margin: 0 5px;

    opacity: 1;
    transition: background-color 0.3s ease,
                border 0.3s ease;
}

ks-content-banners .swiper-pagination-bullet-active  {
    background-color: white;
    opacity: 1;
}



@media (max-width: 638px) {
    ks-content-banners .pagination {
        display: none;
    }
}

ks-content-banners[home] {
    margin-bottom: 20px;
}

ks-content-banners[home] .pagination {
    bottom: -35px;
}

ks-content-banners[home] .swiper-pagination-bullet {
    background-color: rgba(29, 29, 29, 0.22);
}

ks-content-banners[home] .swiper-pagination-bullet:hover  {
    background-color: rgba(29, 29, 29, 0.192);
}

ks-content-banners[home] .swiper-pagination-bullet-active  {
    background-color: rgb(29, 29, 29) !important;
}