ks-description-text-image {
    display: flex;
    align-items: center;
    justify-content: center;

    flex-wrap: wrap;
    padding: 0;
    margin: 0 auto 30px auto;
    max-width: 1200px;
}

ks-description-text-image[alignment="top"] {
    align-items: flex-start;
}

ks-description-text-image[alignment="middle"] {
    align-items: center;
}

ks-description-text-image[alignment="bottom"] {
    align-items: flex-end;
}

ks-description-text-image[position="left"] {
    flex-direction: row-reverse;
}

ks-description-text-image .image {
    width: 100%;
    margin: 20px 0px 0px 0px;
}

@media (min-width: 960px) {
    ks-description-text-image .text {
        flex: 1 !important;
    }

    ks-description-text-image .image {
        width: 50%;
        margin: 0px 0px 0px 30px;
        max-width: 500px;
    }

    ks-description-text-image[position="left"] .image {
        margin: 0px 30px 0px 0px;
    }
}