import { Component, h, Host, Prop } from '@stencil/core';
import { FeaturedWithText, FeaturedWithTextItem } from '../../../global/data/content';

@Component({
	tag: 'ks-content-featured-with-text',
	styleUrl: 'featured-with-text.css'
})
export class ContentFeaturedWithText {
    @Prop() content: FeaturedWithText;

    render() {
        return <Host style={this.containerStyle()}>
            {this.content.featured.map(item => this.featured(item))}
        </Host>;
    }

	featured(item: FeaturedWithTextItem) {
		return <div class="featured" style={this.bannerStyle(item)}>
            <ks-img3 fit={item.fit}
                image={item.image}
                webp={item.webp}
                alt={item.name}
                width={item.width}
                height={item.height}
            ></ks-img3>
            { item.mainText || item.aboveText ?
                <div class={"overlay " + this.alignmentClass(item)}>
                    <div class="text">
                        {item.aboveText ? <div class="aboveText">{item.aboveText}</div> : null}
                        {item.mainText}
                    </div>
                </div>
            : null}
		</div>;
	}

    containerStyle() {
        return {
            gap: `${this.content.gap}px` || 'unset'
        }
    }

    bannerStyle(item: FeaturedWithTextItem) {
        return {
            backgroundColor: item.backgroundColor,
            color: item.textColor
        }
    }

    alignmentClass(item: FeaturedWithTextItem) {
        const horizontal = item.horizontalAlignment || "center";
        const vertical = item.verticalAlignment || "center";

        return `horizontal-${horizontal} vertical-${vertical}`;
    }
}