import { Component, h, Prop, Host } from '@stencil/core';
import { Button } from "../../../global/data/content";

@Component({
  tag: 'ks-content-button',
  styleUrl: 'button.css'
})
export class ContentButton {
    @Prop() text: string;
    @Prop() content: Button;

    render() {
		return <Host class={this.content.align}>
            <ks-button
                name={this.content.name}
                link={this.content.link}
                nofollow={this.content.nofollow}
                round={this.content.round}

                tall={this.content.size == 'tall'}
                narrow={this.content.size == 'narrow'}
                narrower={this.content.size == 'narrower'}

                border={this.content.style == 'border'}
                primary={this.content.style == 'primary'}
                secondary={this.content.style == 'secondary'}
                transparent={this.content.style == 'transparent'}
            ></ks-button>
        </Host>;
    }
}
