ks-content-recipes {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
    max-width: 1200px;
    margin: 20px auto;

    width: 100%;
    min-height: 10px;

    padding: 10px;
    box-sizing: border-box;
}

ks-content-recipes > * {
    width: calc(25% - 10px);
    margin: 5px;
    box-sizing: border-box;
}

@media screen and (max-width: 639px){
    ks-content-recipes > * {
        width: 100%;
    }
}

@media screen and (min-width: 640px) and (max-width: 959px){
    ks-content-recipes > * {
        width: calc(50% - 10px);
    }
}

@media screen and (min-width: 960px) and (max-width: 1199px){
    ks-content-recipes > * {
        width: calc(33.3333333333% - 10px);
    }
}