ks-content-about {
    display: block;
    text-align: center;
    padding: 10px;
}

ks-content-about .main-image {
    width: 150px;
    margin: auto;
}

ks-content-about h2 {
    margin: 15px 0 10px 0;
}

ks-content-about p,
ks-content-about h2,
ks-content-about h3 {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

ks-content-about .items {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}

ks-content-about .item {
    max-width: 300px;
    padding: 7px;
}

ks-content-about .item ks-img3 {
    width: 50px;
    height: 50px;
    margin: 0px auto 10px auto;
}

ks-content-about .item h3 {
    margin-bottom: 7px;
}

ks-content-about ks-button {
    display: none;
}

@media (max-width: 1100px) {
    ks-content-about.expand .items {
        display: block;
    }

    ks-content-about.expand .item {
        max-width: 100%;
    }
}

@media (max-width: 800px) {
    ks-content-about ks-button {
        display: block;
    }

    ks-content-about.expand .items {
        display: none;
    }

    ks-content-about[expanded].expand .items {
        display: flex;
    }
}