import { Component, h, Prop, State, Host, Element } from '@stencil/core';
import { Banner } from '../../../global/data/content';
import { window_load } from '../../deferredpromise';

import Swiper, {Pagination, Autoplay} from 'swiper';

Swiper.use([Pagination, Autoplay]);


@Component({
	tag: 'ks-content-banners',
	styleUrl: 'banners.css'
})
export class ContentBannerWithText {
    @Element() root: HTMLElement;
	@Prop() delay: number = 0;
    @Prop() autoplay: number = 5000;

    @Prop() content: Banner[];

    @Prop({reflect: true}) home: boolean;
    
    carousel: Swiper;
    window: boolean = false;
    rendered: boolean = false;
    @Prop({reflect: true, mutable: true}) loaded: boolean = false;
    @State() activeIndex: number = 0;

    render() {
        return <Host>
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    {this.content.map((item, index) => this.banner(item, index, index == 0))}
                </div>
            </div>
            <div class="pagination">
                <div class="swiper-pagination"/>
            </div>
        </Host>;
    }

	banner(item: Banner, index: number, sync: boolean) {
		return <a class="banner swiper-slide" style={this.bannerStyle(item)} href={item.link ? item.link : null}>
            <ks-img3 fit={item.fit}
                    sync={sync}
                    image={item.image}
                    webp={item.webp}
                    alt={item.name}
                    width={item.width}
                    height={item.height}
            ></ks-img3>
            <div class={"text " + this.alignmentClass(item)}>
                {item.aboveText ? <div class="aboveText">{item.aboveText}</div> : null}
                {item.mainText ? <div class="mainText">{item.mainText}</div> : null}
            </div>
            {item.theme && index == this.activeIndex ?
                <style innerHTML={this.bannerTheme(item)} />
            : null}
		</a>;
	}

    bannerStyle(item: Banner) {
        return {
            backgroundColor: item.backgroundColor,
            color: item.textColor
        }
    }

    bannerTheme(item: Banner) {
        return `:root {
			--navbar-color: ${item.theme.navbarColor} !important;
			--navbar-color-hover: ${item.theme.navbarColorHover} !important;
			--navbar-color-active: ${item.theme.navbarColorActive} !important;
			--navbar-category-color: ${item.theme.categoryColor} !important;
			--navbar-category-hover: ${item.theme.categoryColorHover} !important;
			--navbar-category-active: ${item.theme.categoryColorActive} !important;
			--navbar-category-backdrop: ${item.theme.categoryColorBackdrop} !important;
		}`
    }

    alignmentClass(item: Banner) {
        const horizontal = item.horizontalAlignment || "center";
        const vertical = item.verticalAlignment || "center";

        return `horizontal-${horizontal} vertical-${vertical}`;
    }

    componentDidLoad() {
        window_load.promise.then(()=>this.initialize());
    }

    initialize() {
        setTimeout(() => {
            this.carousel = new Swiper('ks-content-banners .swiper-container', {
                observer: true,
                observeParents: true,

                centeredSlides: true,
                slidesPerView: "auto",
                preventInteractionOnTransition: true,

                grabCursor: true,
                autoplay: {
                    delay: this.autoplay,
                },
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                }
            });

            this.carousel.on('slideChange', () => {
                this.activeIndex = this.carousel.activeIndex;
                console.log(this.activeIndex);
            })

            this.loaded = true;
        }, this.delay);
    }
}