ks-content-featured {
    display: block;
    columns: 2;
    column-gap: 10px;
    
    max-width: 1100px;
    margin: auto;
    
    padding: 0 10px;
    box-sizing: border-box;
}

ks-content-featured .featured {
    display: block;
    overflow: hidden;
    border-radius: 10px;
    margin-bottom: 10px;

    opacity: 1.0;
    transition: opacity 0.3s ease;
}

ks-content-featured .featured:hover {
    opacity: 0.85;
}

ks-content-featured .featured:active {
    opacity: 0.7;
}

ks-content-featured .featured > * {
    height: 100%;
}

@media screen and (max-width: 720px) {
    ks-content-featured {
        columns: 1;
    }
}
