import { Component, h, Prop } from '@stencil/core';
import { marked } from 'marked';

@Component({
  tag: 'ks-description-text-image',
  styleUrl: 'description-text-image.css'
})
export class DescriptionTextImage {
    @Prop() text: string;
    @Prop() image: string;
    @Prop() width: number;
    @Prop({reflect: true}) alignment: string = "top";
    @Prop({reflect: true}) position: string = "right";

    render() {
        return[
            <div class="text" innerHTML={marked.parse(this.text)}></div>,
            <div class="image" style={{maxWidth: `${this.width}px`}}>
                <ks-img2 horizontal limit src={this.image} />
            </div>
        ];
    }
}
