import { Component, h, Prop, Host } from '@stencil/core';
import { Separator } from "../../../global/data/content";

@Component({
  tag: 'ks-content-separator',
  styleUrl: 'separator.css'
})
export class ContentSeparator {
    @Prop() text: string;
    @Prop() content: Separator;

    render() {
		return <Host class={this.content.spacing}>
            {this.content.kind == "line" ? <hr/> : null}
        </Host>;
    }
}
