ks-content-link-tiles {
    display: flex;
    flex-wrap: wrap;
}

ks-content-link-tiles .tile {
    flex: 1 1 350px;
    height: 280px;
    position: relative;
    background-color: #000000;
}


ks-content-link-tiles .tile ks-img3 {
    height: 100%;
    width: 100%;

    transition: opacity 0.3s ease;
    opacity: 1;
}

ks-content-link-tiles .tile:hover ks-img3 {
    opacity: 0.5;
}

ks-content-link-tiles .tile ks-img3:after {
    content: "";
    position: absolute;
    inset: 0px;
    background: linear-gradient(180deg, rgb(0 0 0 / 60%) 0%, rgba(0,0,0,0) 35%);
}

ks-content-link-tiles .tile .text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    position: absolute;
    inset: 0px;

    padding: 15px 20px;

    color: #ffffff;
}

ks-content-link-tiles .tile .heading {
    font-family: var(--font-emphasis);
    font-size: 22px;
    text-shadow: 0 0 7px black;
}

ks-content-link-tiles .tile .links {
    opacity: 0;
    transition: opacity 0.3s ease;
}

ks-content-link-tiles .tile:hover .links {
    opacity: 1;
}

ks-content-link-tiles .tile .links a {
    display: block;
    color: white !important;
    text-decoration: none !important;
    text-shadow: 0 0 4px black;
}

ks-content-link-tiles .tile .links a span {
    display: inline-block;
    transform: translateX(0px);
    transition: transform 0.3s ease;
}

ks-content-link-tiles .tile .links a:hover span {
    transform: translateX(10px);
}

ks-content-link-tiles .tile .links a ks-icon {
    margin-left: -5px;
}

@media screen and (max-width: 640px) {
    ks-content-link-tiles .tile {
        height: 220px;
    }

    ks-content-link-tiles .tile .heading {
        font-size: 18px;
    }

    ks-content-link-tiles .tile .text {
        padding: 10px 15px;
    }

    ks-content-link-tiles .tile .links {
        opacity: 1;
    }

    ks-content-link-tiles .tile ks-img3 {
        opacity: 0.6;
    }
}

@media screen and (max-width: 370px) {
    ks-content-link-tiles .tile .links {
        font-size: 13px;
    }
}