ks-content-thumbnails {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 5px;
    
    max-width: 1200px;
    margin: auto;
    padding: 0 10px;
    box-sizing: border-box;
}

ks-content-thumbnails .thumbnail {
    flex: 1 1 300px;
    max-width: 290px;
    aspect-ratio: 2 / 1;
    border-radius: 10px;
    overflow: hidden;

    opacity: 1.0;
    transition: opacity 0.3s ease;
}

ks-content-thumbnails .thumbnail:hover {
    opacity: 0.85;
}

ks-content-thumbnails .thumbnail:active {
    opacity: 0.7;
}

ks-content-thumbnails .thumbnail > * {
    height: 100%;
}

@media screen and (max-width: 500px) {
    ks-content-thumbnails .thumbnail {
        max-width: 100%;
    }
}
