import { Component, h, Prop } from '@stencil/core';
import { ProductCardData } from '../../../global/data/listing';

@Component({
  tag: 'ks-description-products',
  styleUrl: 'description-products.css'
})
export class DescriptionProducts {
    @Prop() products: ProductCardData[];
    @Prop({reflect: true}) kind: string;

    render() {
        return this.products.map( product =>
            this.kind == "standard" ?
                <ks-product-card flat product={product}/> :
                <ks-product-simple
                    name={product.name}
                    img={product.image}
                    link={product.link}
                    currentPrice={product.currentPrice}
                    previousPrice={product.previousPrice}
                />
        );
    }
}
