ks-content-button {
    display: block;
    margin-bottom: 20px;
}

@media screen and (min-width: 420px) {
    ks-content-button.center {
        display: flex;
        justify-content: center;
    }
    
    ks-content-button.left {
        display: flex;
        justify-content: flex-start;
    }
    
    ks-content-button.right {
        display: flex;
        justify-content: flex-end;
    }

    ks-content-button a {
        padding-left: 40px;
        padding-right: 40px;
    }
}