ks-description-products {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: stretch;

    gap: 10px;
    
    border-top: #f2f2f2 solid 1px;
    border-bottom: #eeeeee solid 1px;

    padding: 10px 10px 20px 10px;
}

ks-description-products > * {
    width: 100%;
    box-sizing: border-box;
}


ks-description-products[kind='standard'] {
    border: none;
}

ks-description-products[kind='simple'] {
    margin-bottom: 20px;
}

@media screen and (max-width: 640px) {
    ks-description-products > * {
        width: 228px;
    }

    ks-description-products[kind='simple'] {
        margin-bottom: 20px;
    }
}

@media (min-width: 360px) and (max-width: 640px) {
    ks-description-products > * {
        width: 48% !important;
    }
}