ks-content-featured-with-text {
    display: flex;
    flex-wrap: wrap;
}

ks-content-featured-with-text .featured {
    flex: 1 1 500px;
    position: relative;
    background-color: #000000;
    height: 500px;
}

ks-content-featured-with-text .featured ks-img3 {
    height: 100%;
    width: 100%;

    transition: opacity 0.3s ease;
    opacity: 1;
}

ks-content-featured-with-text .featured .overlay {
    position: absolute;
    inset: 0px;

    display: flex;
    flex-direction: column;

    max-width: 1000px;
    box-sizing: border-box;
    margin: auto;
}

ks-content-featured-with-text .featured .overlay.horizontal-left   { text-align: left; }
ks-content-featured-with-text .featured .overlay.horizontal-center { text-align: center; }
ks-content-featured-with-text .featured .overlay.horizontal-right  { text-align: right; }

ks-content-featured-with-text .featured .overlay.vertical-top      { justify-content: flex-start; }
ks-content-featured-with-text .featured .overlay.vertical-center   { justify-content: center; }
ks-content-featured-with-text .featured .overlay.vertical-bottom   { justify-content: flex-end; }

ks-content-featured-with-text .featured .text {
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(7px);
    padding: 10px 20px;
    font-size: 22px;
    font-family: var(--font-emphasis);
}

ks-content-featured-with-text .featured .text .aboveText {
    font-size: 16px;
    margin-bottom: -4px;
}


@media screen and (max-width: 1000px) {
    ks-content-featured-with-text .featured {
        height: 400px;
    }
}

@media screen and (max-width: 640px) {
    ks-content-featured-with-text .featured {
        height: 300px;
    }

    ks-content-featured-with-text .featured .text {
        padding: 15px;
    }

    ks-content-featured-with-text .featured .text .mainText {
        padding: 6px 15px;
        font-size: 18px;
    }

    ks-content-featured-with-text .featured .text .aboveText {
        font-size: 14px;
        margin-bottom: -4px;
    }
}