import { Component, h, Prop } from '@stencil/core';
import { Recipes } from '../../../global/data/content';

@Component({
	tag: 'ks-content-recipes',
	styleUrl: 'recipes.css'
})
export class ContentRecipes {
    @Prop() content: Recipes;

    render() {
        return this.content.recipes.map(recipe =>
            <ks-recipe-card flat heading={recipe.heading} cuisine={recipe.cuisine} category={recipe.category}
                link={recipe.link} image={recipe.image} webp={recipe.webp}
                width={recipe.width} height={recipe.height} cuisine-link={recipe.cuisineLink} category-link={recipe.categoryLink} />
        );
    }
}