import { Component, h, Host, Prop } from '@stencil/core';
import { LinkTiles, LinkTilesItem } from '../../../global/data/content';

@Component({
	tag: 'ks-content-link-tiles',
	styleUrl: 'link-tiles.css'
})
export class ContentBannerWithText {
    @Prop() content: LinkTiles;

    render() {
        return <Host style={this.containerStyle()}>
            {this.content.tiles.map(item => this.tile(item))}
        </Host>;
    }

	tile(item: LinkTilesItem) {
		return <div class="tile">
            <ks-img3 fit="cover"
                image={item.image}
                webp={item.webp}
                alt={item.name}
                width={item.width}
                height={item.height}
            ></ks-img3>
            <div class="text">
                <div class="heading">{item.name}</div>
                <div class="links">
                    {item.links.map(link => 
                        <a href={link.href}><ks-icon name="chevron-right"/><span>{link.name}</span></a>
                    )}
                </div>
            </div>
		</div>;
	}

    containerStyle() {
        return {
            gap: `${this.content.gap}px` || 'unset'
        }
    }
}