import { Component, h, Prop, Host } from '@stencil/core';
import { marked } from 'marked';

@Component({
  tag: 'ks-description-text',
  styleUrl: 'description-text.css'
})
export class DescriptionText {
    @Prop() text: string;
    @Prop() align: "left" | "center" | "right";
    @Prop() nomargin: boolean;


    render() {
		const nomargin = this.nomargin ? "nomargin" : "";
		return <Host class={[this.align, nomargin].join(" ")} innerHTML={marked.parse(this.text)} />;
    }
}
