import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-description-image',
  styleUrl: 'description-image.css'
})
export class DescriptionImage {
    @Prop() image: string;
    @Prop() width: number;

    render() {
        return <div class="image" style={{maxWidth: `min(100%,${this.width}px)`}}>
          <ks-img2 src={this.image} />
        </div>;
    }
}
