import { Component, h, Prop } from '@stencil/core';
import { FeaturedBanner } from '../../../global/data/content';

@Component({
	tag: 'ks-content-featured',
	styleUrl: 'featured.css'
})
export class ContentFeatured {
    @Prop() content: FeaturedBanner[];

    render() {
        return this.content.map(item =>
            <a class="featured" href={item.link}>
                <ks-img3 fit="cover"
                    image={item.image}
                    webp={item.webp}
                    alt={item.name}
                    width={item.width}
                    height={item.height}
                ></ks-img3>
            </a>
        );
    }
}